<template>
  <div class="bind-card">
      <h5>请填写您的银行卡信息，本平台暂只支持储蓄卡认证和提现。</h5>
      <div class="bind-card-input">
          <div class="input-item">
              <div class="input-label">姓名</div>
              <div class="input-flex">
                <template v-if="identifyFlag === 1 || identifyFlag === '1'">
                  <input type="text" v-model="realName" placeholder="请输入法人姓名" maxlength="50" class="omit">
                </template>
                <template v-else>
                  <input type="text" v-model="name" :disabled="userAccount!=2">
                </template>
              </div>
              <div class="input-clear" v-show="identifyFlag === 1 && realName.length" @click="clearFn('name')"></div>
          </div>
          <div class="input-item">
              <div class="input-label">身份证号</div>
              <div class="input-flex">
                <template v-if="identifyFlag === 1 || identifyFlag === '1'">
                  <input type="text" v-model="realidcard" placeholder="请输入法人身份证号" maxlength="18">
                </template>
                <template v-else>
                  <input type="text" v-model="idcard" :disabled="userAccount!=2">
                </template>
              </div>
              <div class="input-clear" v-show="identifyFlag === 1 && realidcard.length" @click="clearFn('idcard')"></div>
          </div>
          <div class="input-item">
              <div class="input-label">银行卡号</div>
              <div class="input-flex">
                <input type="text" v-model="bankcard" placeholder="请输入银行卡号" maxlength="24" @input="cardInputFn">
              </div>
              <div class="input-clear" v-show="bankcard.length" @click="clearFn('bankcard')"></div>
          </div>
          <div>
            <div class="input-item">
                <div class="input-label">卡类型</div>
                <div class="input-flex" @click="changeCardType"><p>{{cardName}}</p></div>
                <div class="input-more" v-show="!isCardValild"><van-icon name="arrow" color="#aaa"/></div>
            </div>
            <div class="input-item" :class="{'input-item-noborder': bankcard.length}">
                <div class="input-label">手机号</div>
                <div class="input-flex">
                  <input type="tel" v-model="phone" placeholder="请输入银行预留手机号" @input="phoneInputFn" maxlength="11">
                </div>
                <div class="input-clear" v-show="phone.length" @click="clearFn('phone')"></div>
                <div class="input-notice" @click="noticeFn"></div>
            </div>
          </div>
      </div>
      <div class="bind-card-protocol">
          <van-checkbox v-model="isAgree" icon-size=".3rem" @change="agreeChange"/>
          <router-link to="/documentInfo?id=915&title=%E5%AE%9E%E5%90%8D%E8%AE%A4%E8%AF%81%E6%9C%8D%E5%8A%A1%E5%8D%8F%E8%AE%AE">同意智家商城支付相关协议</router-link>
      </div>
      <div class="bind-card-btn">
          <a href="javascript:;" :class="{'active': checkCanNext}" @click="doNext">下一步</a>
          <p>本服务由海尔海融易服务</p>
      </div>
      <van-popup
        v-model="cardMore"
        closeable
        close-icon-position="top-right"
        position="bottom"
        :style="{'height': '90%'}"
      >
        <div class="pop-box">
          <h6>选择所属银行</h6>
          <div>
            <banksList v-on:selected="bankSelectFn"/>
          </div>
        </div>
      </van-popup>
  </div>
</template>

<script>
import URL from '@/http/url'
import utils from '@/utils/utils'
import banksList from '@/components/banksList'

import { mapMutations } from 'vuex'

export default {
  name: 'bindcard',
  data() {
    return {
      name: '', // 隐私姓名
      realName: '', // 全姓名
      idcard: '', // 隐私证件号
      realidcard: '', // 全证件号
      bankcard: '', // 卡号
      bankPhone: '', // 银行电话
      cardtype: '', // 银行卡类型
      bankCode: '', // 银行卡code
      cardName: '请选择卡类型', // 银行卡名称
      isCardValild: false, // 是否能够识别银行卡
      phone: '', // 预留手机号
      cardMore: false,
      isAgree: true, // 是否同意协议
      canNext: false, // 按钮可点击
      identifyFlag: '',
      userAccount: '', // 用户身份识别  1对公  2对私  3普通用户
      destination: undefined, // 成功后跳转目标地址
    }
  },
  components: {
    banksList,
  },
  mounted() {
    this.identifyFlag = this.$route.query.identifyFlag
    this.destination = this.$route.query ? this.$route.query.destination : undefined
    // this.fetchInit()
  },
  computed: {
    // showBottom() {
    //   return this.realName && this.realidcard.length === 18 && this.bankcard.length > 8
    // }
    checkCanNext() {
      return this.realName && this.realidcard.length === 18 && this.bankcard && this.cardtype && utils.isPhone(this.phone) && this.isAgree
    }
  },
  watch: {
    // showBottom(val) {
    //   // 重置数据
    //   if (!val) {
    //     this.cardName = '请选择卡类型'
    //     this.phone = ''
    //   }
    // }
    identifyFlag (newValue) {
      if (newValue !== '') {
        this.fetchInit()
      }
    },
  },
  methods: {
    ...mapMutations('bankcard', ['setBankRegInfo']),
    doNext() {
      if (!this.checkCanNext) {
        return false
      }
      let url = `${URL.POST_CHECKS}?ownerName=${this.realName}&identityNo=${this.realidcard}&cardNo=${this.bankcard.replace(/\s/g, '')}&mobile=${this.phone}`
      this.$http.post(url).then(res => {
        let result = res.data.data
        if (result.isThrough) {
          this.sendCode()
        } else {
          this.$toast(res.data.message)
        }
      })
      // 测试 删除 start
      // this.setBankRegInfo({
      //   bankPhone: this.bankPhone,
      //   bankCode: this.bankCode,
      //   phone: this.phone,
      //   name: this.realName,
      //   cardNo: this.bankcard.replace(/\s/g, ''),
      //   identityNo: this.realidcard,
      // })
      // this.$router.replace('/verificationcode')
      // 测试 删除 end
    },
    sendCode() {
      this.$http.get(URL.GET_CODEID + '?mobile=' + this.phone).then(res => {
        let result = res.data
        if (result.success) {
          this.setBankRegInfo({
            bankPhone: this.bankPhone,
            bankCode: this.bankCode,
            phone: this.phone,
            name: this.realName,
            cardNo: this.bankcard.replace(/\s/g, ''),
            identityNo: this.realidcard,
          })

          if (!this.destination) {
            if (window.location && window.location.href.indexOf('sg_rn_app') !== -1) {
              this.$router.replace('/verificationcode?sg_rn_app')
            } else {
              this.$router.replace('/verificationcode')
            }
          } else {
            this.$router.replace(`/verificationcode?destination=${this.destination}`)
          }
        }
      })
    },
    // checkCanNext() {
    //   if (this.realName && this.realidcard.length === 18 && this.bankcard && this.cardtype && utils.isPhone(this.phone) && this.isAgree) {
    //     this.canNext = true
    //   } else {
    //     this.canNext = false
    //   }
    // },
    phoneInputFn() {
      // this.checkCanNext()
    },
    cardInputFn(ev) {
      let value = ev.target.value
      // if (ev.keyCode === 8) {
      //   if (value) {
      //     for (let i = 0; i < value.length; i++) {
      //       var newStr = value.replace(/\s$/g, '');
      //     }
      //     this.bankcard = newStr;
      //   }
      // } else {
      //   for (let i = 0; i < value.length; i++) {
      //     var arr = value.split('');
      //     if ((i + 1) % 5 === 0) {
      //       arr.splice(i, 0, ' ');
      //     }
      //   }
      //   this.bankcard = arr.join('');
      // }
      this.bankcard = value.replace(/(\d{4})(?=\d)/g, '$1 ')

      if (this.bankcard.length === 7) {
        this.getBankName()
      }
      // this.checkCanNext()
    },
    // 获取银行卡名称
    getBankName() {
      let cardNo = this.bankcard.replace(/\s/g, '')
      this.$http.get(URL.GET_BANKNAME + '?cardNo=' + cardNo).then(res => {
        let result = res.data.data
        if (result.bankNames) { // 能够识别银行卡
          this.cardtype = result.bankNames.bankNo
          this.cardName = result.bankNames.bankName
          this.bankCode = result.bankNames.bankCode
          this.bankPhone = result.bankPhone
          this.isCardValild = true
        } else { // 不能识别
          this.cardtype = ''
          this.cardName = '请选择卡类型'
          this.isCardValild = false
        }
      })
    },
    // 切换选择银行类型
    changeCardType() {
      if (this.isCardValild) return
      this.cardMore = true
    },
    clearFn(type) {
      switch (type) {
        case 'name':
          this.realName = ''
          break;
        case 'idcard':
          this.realidcard = ''
          break;
        case 'phone':
          this.phone = ''
          break;
        case 'bankcard':
          this.bankcard = ''
          break;
        default:
          break;
      }
      // this.checkCanNext()
    },
    fetchInit() {
      this.$http.get(URL.TRUE_REALNAMEAUTH).then(res => {
        let result = res.data.data;
        if (result.isAuth) {
          // if (this.identifyFlag !== 1 && this.identifyFlag !== '1') this.fetchData()
          this.fetchData()
        } else {
          if (this.identifyFlag !== 1 && this.identifyFlag !== '1') {
            this.$dialog.confirm({
              title: '您还未进行实名认证，请先去认证'
            }).then(() => {
              if (window.location && window.location.href.indexOf('sg_rn_app') !== -1) {
                this.$router.replace('/trueAuthentication?sg_rn_app')
              } else {
                this.$router.replace('/trueAuthentication')
              }
            }).catch(() => {
              // this.$router.back()
              this.$store.dispatch('doHistoryBack')
            });
          }
        }
      })
    },
    fetchData() {
      this.$http.get(URL.GET_BANK_CARD_INFO).then(res => {
        if (!res.data.success) {
          this.$toast(res.data.message)
          return
        }
        let result = res.data.data
        this.realName = result.realName
        this.realidcard = result.identityNo
        this.idcard = result.identityNo
        this.name = result.realName

        this.userAccount = result.accountStatus
        if (+this.userAccount === 3) {
          this.idcard = result.identityNo.replace(/(\d{6})(\d+)(\d{2})/, function(x, y, z, p) {
            var i = '';
            while (i.length < z.length) { i += '*' }
            return y + i + p
          });
          this.name = result.realName.replace(/.{1}/, '*');
        }
      })
    },
    bankSelectFn(item) {
      this.cardMore = false
      this.cardtype = item.code
      this.cardName = item.name
      // this.checkCanNext()
    },
    agreeChange() {
      // this.checkCanNext()
    },
    noticeFn() {
      this.$dialog.alert({
        message: '<p>银行预留手机号是您在办理该银行卡时所填写的手机号。如忘记、停用或无预留手机号，请拨打银行客服电话 <a href="tel:' + this.bankPhone + '" style="color: #1989fa">' + this.bankPhone + '</a> 更新处理!</p>'
      }).then(() => {
        // on close
      });
    }
  }
}
</script>

